
import redirect from '../../function/redirect'
const extractData = (response) => {
    return new Promise((resolve, reject) => {
        let {
            status,
            statusText,
            data: {
                code,
                data,
                msg
            }
        } = response
        if (status != 200) {
            reject(response)
        } else if (code != 0) {
            reject(response)
        } else {
            resolve(data)
        }
    })
}

const handleError = (error) => {
    let errMsg;
    if (error instanceof Object) {
        let status = error.status
        if (status == 401) {
            errMsg = ''
        } else if (status == 402) {
            errMsg = ''
        } else if (status == 404) {
            errMsg = ''
        } else {
            const body = error.data || '';
            if(body.code == 501){
                //status401
                //erp
                var currentUrl = window.location.href;
                var encodeUrl = encodeURIComponent(currentUrl);
                redirect(`/login.html?url${encodeUrl}`);
            } else {
                errMsg = body.msg || JSON.stringify(body);
            }
        }
    } else {
        errMsg = error.message ? error.message : error.toString();
    }
    throw errMsg
}

const interceptor = (request, next) => {
    // storage.getItem('token').then(token => {
    //     request.headers.set('HTTP_ACCESS_TOKEN', token)
    // }).finally(()=>{
    //     next()
    // })
    next()
}

//restful
export default function() {
    ['get', 'delete', 'head', 'jsonp'].forEach(method => {
        let _method = Vue.http[method]
        Vue.http[method] =function(url, options) {
            return _method.call(this, url, options).then(response => {
                return extractData(response)
            }).catch(err => {
                return handleError(err)
            })
        }
    });
    ['post', 'put', 'patch'].forEach(method => {
        let _method = Vue.http[method]
        Vue.http[method] = function(url, body, options) {
            return _method.call(this, url, body, options).then(response => {
                return extractData(response)
            }).catch(err => {
                return handleError(err)
            })
        }
    });
    return interceptor
}