
const plugin = {
  install (Vue) {
        Vue.prototype.$toast = (message, type)=>{
            debugger
            var payload = {message, type};
            window.store.dispatch('TOAST', payload);
        }
        Vue.toast = (message, type) => {
            debugger
            var payload = {message, type};
            window.store.dispatch('TOAST', payload);
        }
  },
}

export default plugin
export const install = plugin.install