<template>
    <el-table
            border
            style="width: min-content;"
            v-bind="$attrs"
            v-on="$listeners"
            :row-style="rowStyle"
            :header-cell-style="{backgroundColor:'#53a6e4',color:'#ffffff'}"
            :width="1000">
        <template v-for="(_, slot) of $scopedSlots" v-slot:[slot]="scope">
            <slot :name="slot" v-bind="scope"/>
        </template>
        <slot/>
    </el-table>
</template>

<script>
  export default {
    name: "TablePlus",
    methods: {
      rowStyle(param) {
        return param.rowIndex%2?{background:'#f5f3f4'}:{background:'white'}
      },
    },
  }
</script>


