
'use strict';

import * as localStorageCtr from 'library/util/localStorage';

export default {
	'TOAST' (state, payload) {
		state.showToast = true;
		state.toastMessage = payload.message;
		if (payload.type) {
			state.toastType = payload.type;
		} else {
			state.toastType = 'warning';
		}
	},
	'HIDE_TOAST' (state) {
		state.showToast = false;
		state.toastMessage = '';
	},
  'SAVE_SELECTION' (state,payload) {
    state.selection = {...payload};
  },
}
