/**
 * @fileOverview url
 */

'use strict';

/**
 *  $.param
 *
 * @param {string} queryStr  location.search  location.hash 
 * @return {Object}
 */
export function parseQuery (queryStr) {

    let result = { };

    if ($.type(queryStr) === 'string' && queryStr.length > 1) {

        let startIndex = 0;

        let firstChar = queryStr.charAt(0);

        // query  ?a=1
        if (firstChar === '?') {
            startIndex = 1;
        }
        // hash  #a=1
        else if (firstChar === '#') {
            startIndex = 1;

            let secondChar = queryStr.charAt(1);
            // hash  #/a=1&b=2
            if (secondChar === '/') {
                startIndex = 2;
            }
        }

        if (startIndex > 0) {
            queryStr = queryStr.substr(startIndex);
        }
        $.each(
            queryStr.split('&'),
            function (index, item) {
                let parts = item.split('=');
                if (parts.length === 2) {
                    let key = $.trim(parts[0]);
                    let value = $.trim(parts[1]) || '';

                    let specialChar = value.indexOf('#');


                    if (key.indexOf('?') > 0) {
                        key = key.split('?')[1];
                    }
                    if (specialChar >= 0) {
                        value = value.substring(0, specialChar);
                    }
                    if (key) {
                        result[key] = decodeURIComponent(value);
                    }
                }
            }
        );
    }

    return result;
}

/**
 *  origin location.origin
 *
 * @param {?string} url
 * @return {string}
 */
export function getOrigin (url) {

    if (!url) {
        url = document.URL;
    }

    return exports.parse(url).origin;

}

/**
 *  url location 
 *
 * @param {string} url
 * @return {Object}
 */
export function parse (url) {

    let link = document.createElement('a');
    link.href = url;

    //  a 
    url = link.href;

    let origin = '';

    if (link.protocol && link.host) {
        origin = link.protocol + '//' + link.host;
    }
    else if (/^(http[s]?:\/\/[^/]+)(?=\/)/.test(url)) {
        origin = RegExp.$1;
    }

    // xp  http  80 
    let terms = origin.split(':');

    if (origin.indexOf('http:') === 0
        && terms.length === 3
        && terms[2] == 80
    ) {
        terms.length = 2;
        origin = terms.join(':');
    }

    let pathname = link.pathname;

    if (pathname && pathname.charAt(0) !== '/') {
        pathname = '/' + pathname;
    }

    return {
        origin: origin,
        pathname: pathname,
        search: link.search
    };
}
