
'use strict';
import App from './app.vue'
import Config from './app.config'
import router from './route';
import store from './store';
import 'element-ui/lib/theme-chalk/index.css'
import 'katex/dist/katex.min.css'
import ElementUI from 'element-ui'
import Print from 'vue-print-nb'
import toast from 'common/plugins/toast'
import $ from 'jquery'
import VueResouce from 'vue-resource'
import VueHtml5Editor from 'vue-html5-editor'
import editorOption from './editorOption'
import './resource/css/main.scss'
import TablePlus from './common/components/TablePlus'

window.$ = $
window.jQuery = $

//
const {config} = Config(Vue)

Vue.use(ElementUI)
Vue.use(Print);
Vue.use(toast)
Vue.use(VueResouce)
Vue.use(VueHtml5Editor,editorOption);
Vue.component(TablePlus.name, TablePlus)
// Vue.use(VuexPatch)
window.commit = store.commit
window.store = store;

// directives

require('../library/dep/date-format');

// polyfill
require('library/polyfill/find');

// filters
require('library/filter/date');



Vue.use(VueRouter);

router.beforeEach(function(to, from, next) {
	var from = from.fullPath;
	clearState(from);
	next();
});


window.router = router;

/**
 * 
 */
function clearState(module) {
    var status = store.state[module];
    if (status) {
        for (var key in status) {
            var property = status[key];
            if (status.hasOwnProperty(key) && property &&
                typeof property === 'boolean') {
                status[key] = false;
            }
        }
    }
}

new Vue({
	el: '#app',
	store: store,
	router: router,
	render: h => h(App),
	data() {
	  return {
		eventHub: new Vue()
	  }
	}
})
