
const state = {
}

const getters = {
}

const mutations = {
  UPDATE_LOADING(state, payload){
    if(payload){
      state.instance = Vue.modal.loading()
    }else if(state.instance){
      state.instance.close()
    }
  },
  UPDATE_TOAST (state, payload) {
    Vue.modal.toast(payload)
  },
}

