
<template>
    <div id="toast" v-show="show">
        <div class="weui_mask_transparent"></div>
          <div class="weui_toast" :class="{warning: type=='warning', 'success': type=='success'}">
            <i v-if="type=='warning'" class="icon icon-times"></i>
            <i v-if="type=='success'" class="icon icon-check-nocircle"></i>
            <p class="weui_toast_content"><slot></slot></p>
        </div>
  </div>
</template>

<script>
    export default {
        props: {
            time: {
                type: Number,
                default: 2000
            },
            type: {
                type: String,
                default: 'warning'
            },
            show: {
                type: Boolean,
                default: false
            }
        },
        watch: {
            show: function(val) {
                const _this = this;
                if (val) {
                    clearTimeout(this.timeout)
                    this.timeout = setTimeout(function() {
                        if (window.commit) {
                            window.commit('HIDE_TOAST');
                        } else {
                            // _this.show = false;
                            _this.$emit('update:show', false)
                        }
                    }, _this.time)
                }
            }
        }
    }
</script>

<style lang="scss">
    .weui_toast {
        position: fixed;
        z-index: 9999;
        width: 180px;
        text-align: center;
        line-height: 30px;
        padding: 10px;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
             -o-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        background: rgba(242, 222, 222, .9);
        color: #a94442;
        border: 1px solid #ebccd1;
        &.success {
            color: #3c763d;
            background: rgba(223, 240, 216 ,0.90);
            border: 1px solid #d6e9c6;
        }
        .icon {
            display: none;
            font-weight: 700px;
            font-size: 20px;
            border-radius: 50%;
            vertical-align: middle;
        }
    }
    .weui_toast_content {
        display: inline-block;
        word-break: break-all;
    }
</style>