
import { convert } from 'library/util/router-convert.js';
import { routes as modules } from './modules';


Vue.use(VueRouter);
const routes = [
  ...modules,
    {
      path: '',
      name: 'home',
      component: import('modules/index/index.vue'),
      meta: {
        requireAuth: true,
      },
    },
    {
      path: '/',
      name: 'default',
      component: import('modules/index/index.vue'),
      meta: {
        requireAuth: true,
      },
    },
    {
      path: '/selection',
      name: 'selection',
      component: () => import('modules/selection/selection.vue')
    },
    {
      path: '/barometer',
      name: 'barometer',
      component: () => import('modules/barometer/barometer.vue')
    },
    {
      path: '/macro',
      name: 'macro',
      component: () => import('modules/macro/macro.vue')
    },
    {
      path: '/meso',
      name: 'meso',
      component: () => import('modules/meso/meso.vue')
    },
    {
      path: '/portfolio',
      name: 'portfolio',
      component: () => import('modules/portfolio/portfolio.vue')
    },
    {
      path: '/trace',
      name: 'trace',
      component: () => import('modules/trace/trace.vue')
    },
    {
      path: '/assist',
      name: 'assist',
      component: () => import('modules/assist/assist.vue')
    },
    {
      path: '/manager/:code/:name',
      name: 'manager',
      component: () => import('modules/selection/managerDetail.vue')
    },
    {
      path: '/fund/:code/:name',
      name: 'fund',
      component: () => import('modules/selection/fundDetail.vue')

    },
    {
      path: '/manager2/:code',
      name: 'manager2',
      component: () => import('modules/manager/managerDetail.vue')

    },
    { path: '*', redirect: '/' }
];



const router = new VueRouter({
  routes: routes
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(r => r.meta.requireAuth)) {
      if (window.localStorage.getItem("token")) {
          next();
      }
      else {
         window.location.href = '/login.html'
          // next({
          //     path: '/login',
          //     query: {redirect: to.fullPath}
          // })
      }
  }
  else {
      next();
  }
});

export default router;