import * as plugins from 'common/plugins'

class AppConfig {
  constructor({ context, plugins }) {
    configPlugin(plugins, context)
    configContext(context)
  }
}

const configPlugin = (plugins, context) => {
  Object.keys(plugins).forEach(key => {
    Vue.use(plugins[key], context)
  })
}
const configContext = (context) => {
  let { HOST } = context
  Vue.http.options.root = HOST
}

export default function Config(Vue) {
  return {
    config: new AppConfig({
      context: process.env,
      plugins: plugins
    })
  }
}