
'use strict';


export function getImgServerUrl() {
    const map = {
        'dev': 'http://files.dev.lookingedu.com/files/uploadFile',
        'review': 'https://files.review.lookingedu.com/files/uploadFile',
        'prod': 'https://files.lookingedu.com/files/uploadFile',
    }
    return map[process.env.ENV];
}

export function getClassUrl(classId) {
    const map = {
        'dev': 'https://mk.dev.zhimo.co/wechat/index.html',
        'review': 'https://mk.review.zhimo.co/wechat/index.html',
        'prod': 'https://mk.zhimo.co/wechat/index.html',
    }
    return `${map[process.env.ENV]}?from=qrcode#/wechat/class/${classId}`;
}
