/**
 * @file 
 */

'use strict';

import { parseQuery } from 'library/util/url';

let redirect = function (url) {
	let search = parseQuery(window.location.href);
	var openId = search.openId;
	var hashSpan = url.indexOf('#');
	var redirectUrl = url;

	if (hashSpan > 0) {
		redirectUrl = url.slice(0, hashSpan);
	}

	if (openId) {
		if (url.indexOf('?') > 0) {
			redirectUrl += '&openId=' + openId;
		} else {
			redirectUrl += '?openId=' + openId;
		}
	}
	if (hashSpan > 0) {
		redirectUrl += url.slice(hashSpan);
	}
	window.location.href = redirectUrl;
};

export default redirect;

